				this.shopInfo.main = this.helpBuyTextArea
			}
			if (this.appraisement) {
				this.shopInfo.price = this.appraisement
			}
			if (this.helpBuyTextArea.trim() && this.appraisement) {
				uni.setStorageSync('shopInfo', this.shopInfo)
